import React, { Component } from 'react';
import Works from '../Works'
import styles from './home.module.scss'

export default class Home extends Component {

    constructor(props) {
        super(props);
        this.showRef = React.createRef();
        this.rootRef = React.createRef();
    }

    state = { opacity: 1, position: "absolute", src: "./1.jpg", innerHTML: "" }

    componentDidMount() {
        const distance0 = this.showRef.current.offsetTop;
        const height = document.documentElement.clientHeight; //documentElement
        console.log('distance0:',distance0);
        console.log('height:',height);
        document.body.onscroll = () => {
            let distance = document.documentElement.scrollTop;
            let current = (distance - distance0) / height; //当前滑过了几个clientHeight
            console.log('current:',current);
            if (current < 0 || current > 6) {
                this.setState({ position: "absolute" });
            }
            else if (current >= 4 && current < 6) { //第3页
                this.setState({
                    opacity: 6 - current, position: "fixed", src: "./home_3.jpg",
                    innerHTML: ["能保持良好的沟通，", "认真负责、积极乐观。"]
                });
            }
            else if (current >= 2) { //第2页
                this.setState({
                    opacity: 4 - current, src: "./home_2.jpg",
                    innerHTML: ["做过.NET、iOS客户端开发，", "参与过嵌入式AI项目实现。"]
                });
            }
            else if (current >= 0) {  //第1页
                this.setState({
                    opacity: 2 - current, position: "fixed", src: "./home_1.jpg",
                    innerHTML: ["了解常见的编程语言与设计模式，", "可较快入手新项目。"]
                });
            }
        };
    }

    render() {
        const { opacity, position, src, innerHTML } = this.state;
        return (
            <div ref={this.rootRef} >
                <div className={styles.home}>
                    <br /><br /><br /><br /><br /><br /><br />
                    <h1 >您好，</h1><br />
                    <h2 >我是朱雷，</h2><br />
                    <h2 className={styles.detail} >一个编程爱好者。</h2>
                    <br /><br /><br /><br />
                    <img className={styles.img} src={process.env.PUBLIC_URL + "./home_0.jpg"} alt=" " />
                    <br /><br />
                </div>

                <div className={styles.show} ref={this.showRef} >
                    <div className={styles.show_text} style={{ position, opacity }}>
                        <p>{innerHTML[0]}<font color="#FF5151"><br /><br />{innerHTML[1]}</font></p>
                    </div>
                    <img className={styles.show_img} src={src} alt="" style={{ position, opacity }} />
                </div>

                <Works />
                <br />
                <div className={styles.copyright}>Copyright ©2021 EliZhu. &nbsp;&nbsp;2435754260@qq.com</div>
                <br /><br />
            </div>
        )
    }
}