import React, { Component } from 'react';
// import { NavLink } from 'react-router-dom';
import styles from "./work.module.scss"

export default class Works extends Component {

    render() {
        const { work } = this.props;
        return (
            work.map((item) => {
                return (

                    <div key={item.id} className={styles.work} style={{ backgroundColor: (item.id & 1) === 1 ? "black" : "white" }}>
                        <br /><br /><br /><br /><br /><br />
                        <h1 style={{ color: (item.id & 1) === 1 ? "white" : "black" }}>{item.title} </h1><br />
                        <h3 style={{ color: (item.id & 1) === 1 ? "lightgray" : "gray" }}>{item.labels}</h3><br />
                        <h3 style={{ color: "gray" }}>{item.description}</h3>
                        <br /><br />
                        <a style={{ color: "#2894FF", textDecoration: "none" }} onClick={e => alert('待添加...')}>进一步了解 ＞</a>
                        {/* <NavLink to={item.path} style={{ color: "#2894FF", textDecoration: "none" }}>进一步了解 ＞</NavLink> */}
                        <br /><br /><br />
                        <img src={item.img} alt="" />
                        <div className={styles.separator} />
                    </div>
                )
            })
        )
    }
}