import React, { Component, Fragment } from 'react';
import styles from './contact.module.scss';
import { Input, Button, Checkbox, Rate, Pagination, List, Avatar } from 'antd';
import "antd/dist/antd.css"

const desc = ['terrible', 'bad', 'normal', 'good', 'wonderful'];
const data = [
    {
        title: 'Ant Design Title 1',
    },
    {
        title: 'Ant Design Title 2',
    },
    {
        title: 'Ant Design Title 3',
    },
    {
        title: 'Ant Design Title 4',
    },
];

export default class Contact extends Component {

    state = { value: 3 };
    handleChange = value => {
        this.setState({ value });
    };

    showData = () => {
        alert("您提交的内容是：\n" + this.comment.resizableTextArea.props.value);
    }

    render() {
        const { value } = this.state;
        const { TextArea } = Input;
        return (
            <Fragment>
                <br /><br /><br /><br />
                <div className={styles.contact}>
                    <div className={styles.board}>
                        <List
                            itemLayout="horizontal"
                            dataSource={data}
                            renderItem={item => (
                                <List.Item>
                                    <List.Item.Meta
                                        avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
                                        title={"ant design"}
                                        description="Ant Design, a design language for background applications, is refined by Ant UED Team"
                                    />
                                </List.Item>
                            )}
                        />
                        <br />
                        <Pagination size="small" total={50} showSizeChanger showQuickJumper />
                    </div>

                    <div className={styles.comment}>
                        <br /><br />
                        <TextArea ref={c => this.comment = c} maxLength={1000} rows={6} cols={80} placeholder="请输入您的留言..." showCount />
                        <br />
                        <span>
                            <Rate tooltips={desc} onChange={this.handleChange} value={value} />
                            {value ? <span className="ant-rate-text">{desc[value - 1]}</span> : ''}
                        </span>
                        <br /><br />
                        <Button type="primary" shape="round" onClick={this.showData}>发表</Button>&nbsp;&nbsp;&nbsp;
                    <Checkbox style={{ fontSize: "small" }} >私密</Checkbox>
                    </div>
                </div>
                <br /><br /><br /><br />
            </Fragment>
        );
    }
}