import React, { Component, Fragment } from 'react';
import { NavLink} from 'react-router-dom'
import styles from './navigation.module.scss'

export default class Navigation extends Component {
    render() {
        return (
            <Fragment>
                <div className={styles.wrap}>
                    <div className={styles.main}>
                        <NavLink activeClassName={styles.active} className={styles.item} to="/home">主页</NavLink>
                        <NavLink activeClassName={styles.active} className={styles.item} to="/works">作品</NavLink>
                        <NavLink activeClassName={styles.active} className={styles.item} to="/contact">留言</NavLink>
                        <div className={styles.item}>待续</div>
                    </div>
                </div>
            </Fragment>
        )
    }
}